import { useState } from "react";
import Layout from "../components/Layout";
import { mockMedicines } from "../mock/data";

export default function CashierPage() {
  const [user] = useState({ name: "ÿ≥ÿßÿ±ÿ©", role: "cashier" });

  const [products] = useState(mockMedicines);
  const [cart, setCart] = useState([]);
  const [search, setSearch] = useState("");

  const addToCart = (item) => {
    setCart([...cart, item]);
  };

  const removeItem = (index) => {
    const updated = [...cart];
    updated.splice(index, 1);
    setCart(updated);
  };

  const total = cart.reduce((sum, item) => sum + item.price, 0);

  const filtered = products.filter(
    (p) =>
      p.name.includes(search) ||
      p.category.includes(search)
  );

  return (
    <Layout user={user} title="üßæ ŸÜŸÇÿ∑ÿ© ÿßŸÑÿ®Ÿäÿπ">
      <div dir="rtl" className="grid grid-cols-1 gap-6 md:grid-cols-2">

        {/* ŸÇÿßÿ¶ŸÖÿ© ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™ */}
        <div>
          <input
            type="text"
            placeholder="üîç ÿ®ÿ≠ÿ´ ÿπŸÜ ŸÖŸÜÿ™ÿ¨"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="w-full px-3 py-2 mb-4 border rounded-md"
          />

          <div className="bg-white border rounded-lg shadow-sm overflow-auto max-h-[500px]">
            <table className="w-full text-sm text-right">
              <thead className="text-gray-600 bg-gray-50">
                <tr>
                  <th className="px-3 py-2">ÿßŸÑÿßÿ≥ŸÖ</th>
                  <th className="px-3 py-2">ÿßŸÑÿ≥ÿπÿ±</th>
                  <th className="px-3 py-2">ÿ•ÿ∂ÿßŸÅÿ©</th>
                </tr>
              </thead>

              <tbody>
                {filtered.map((item) => (
                  <tr key={item.id} className="border-t hover:bg-gray-50">
                    <td className="px-3 py-2">{item.name}</td>
                    <td className="px-3 py-2">{item.price} ÿ±.ÿ≥</td>
                    <td className="px-3 py-2">
                      <button
                        onClick={() => addToCart(item)}
                        className="px-3 py-1 text-white rounded-md bg-sky-600 hover:bg-sky-700"
                      >
                        ‚ûï
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>

            </table>
          </div>
        </div>

        {/* ÿßŸÑÿ≥ŸÑÿ© */}
        <div className="p-4 bg-white border rounded-lg shadow-sm">
          <h3 className="mb-4 text-lg font-semibold">üõí ÿßŸÑÿ≥ŸÑÿ©</h3>

          {cart.length === 0 ? (
            <p className="text-gray-500">ŸÑÿß ÿ™Ÿàÿ¨ÿØ ÿπŸÜÿßÿµÿ± ŸÖÿ∂ÿßŸÅÿ©</p>
          ) : (
            <table className="w-full text-sm text-right">
              <thead className="bg-gray-50">
                <tr>
                  <th className="px-3 py-2">ÿßŸÑÿµŸÜŸÅ</th>
                  <th className="px-3 py-2">ÿßŸÑÿ≥ÿπÿ±</th>
                  <th className="px-3 py-2">ÿ≠ÿ∞ŸÅ</th>
                </tr>
              </thead>
              <tbody>
                {cart.map((item, index) => (
                  <tr key={index} className="border-t hover:bg-gray-50">
                    <td className="px-3 py-2">{item.name}</td>
                    <td className="px-3 py-2">{item.price} ÿ±.ÿ≥</td>
                    <td className="px-3 py-2">
                      <button
                        onClick={() => removeItem(index)}
                        className="px-3 py-1 text-red-600 rounded-md hover:bg-red-50"
                      >
                        üóëÔ∏è
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          )}

          <div className="flex justify-between pt-3 mt-4 text-lg font-semibold border-t">
            <span>ÿßŸÑÿ•ÿ¨ŸÖÿßŸÑŸä:</span>
            <span>{total} ÿ±.ÿ≥</span>
          </div>

          <button
            onClick={() => alert("‚úîÔ∏è ÿ™ŸÖÿ™ ÿπŸÖŸÑŸäÿ© ÿßŸÑÿ®Ÿäÿπ (Mock ŸÅŸÇÿ∑)")}
            className="w-full py-2 mt-4 text-white bg-green-600 rounded-md hover:bg-green-700"
          >
            ‚úîÔ∏è ÿ™ÿ£ŸÉŸäÿØ ÿßŸÑÿ®Ÿäÿπ
          </button>
        </div>

      </div>
    </Layout>
  );
}














